<template>
    <div class="member-message">
        <div class="message-list" :class="{ 'open' : messageMenu }">
            <div class="message-wrap">
                <el-input
                    placeholder="搜尋使用者"
                    prefix-icon="el-icon-search"
                    v-model="user">
                </el-input>
                <ul class="message-item">
                    <li v-for="(item,idx) in 50" :key="idx" class="user">
                        <HeadIcon :width="50"/>
                        <div :class="`info unread`">
                            <p class="name">Martina Kathrine</p>
                            <p>Curabitur libero. Donec faclit...</p>
                        </div>
                    </li>
                </ul>
                <i class="el-icon-arrow-left switch-btn" :class="{'close': messageMenu}" @click="msgMenuClose()"/>
            </div>
        </div>
        <div class="message-info">
            <div class="car-content">
                <img src="~@/assets/img/cartype/photo-1.png" alt="">
                <div class="txt">
                    <p class="name">Aston Martin Vantage</p>
                    <p>In a pickup market gone fancy, the Silverado sticks to its basic-truck recipe. The steering is accurate, and the...</p>
                </div>
                <div class="card-money">
                    <img src="~@/assets/img/cartype/money-icon-w.png" alt="" srcset=""> 
                    <p>149.8 萬</p>
                </div>
            </div>
            <div class="message-box">
                <div class="message-user">
                    <HeadIcon color="#ef6d38" :width="80" />
                    <div>
                        <p class="name">William</p>
                        <p class="date">30 MAY 2015 POSTED AT 12:25 AM</p>
                    </div>
                </div>
                <div class="message-content">
                    <div class="text">Curabitur libero. Donec facilisis velit eudsl est. Phasellus consequat. Aenean vita quam. Vivamus et nunc. Nunc consequat sem velde metus imperdiet lacinia. Dui estter neque molestie necd dignissim ac hendrerit quis purus. Etiam sit amet vec convallis massa scelerisque mattis. Sed placerat leo nec.</div>
                </div>
            </div>
            <div class="message-box reply">
                <div class="message-user">
                    <HeadIcon :width="80" />
                    <div>
                        <p class="name">Martina Kathrine</p>
                        <p class="date">30 MAY 2015 POSTED AT 12:25 AM</p>
                    </div>
                </div>
                <div class="message-content">
                    <div class="text">Curabitur libero. Donec facilisis velit eudsl est. Phasellus consequat. Aenean vita quam. Vivamus et nunc. Nunc consequat sem velde metus imperdiet lacinia. Dui estter neque molestie necd dignissim ac hendrerit quis purus. Etiam sit amet vec convallis massa scelerisque mattis. Sed placerat leo nec.</div>
                </div>
            </div>
            <div class="message-box input">
                <div class="message-user">
                    <HeadIcon color="#ef6d38" :width="80" />
                    <div>
                        <p class="name">William</p>
                    </div>
                </div>
                <div class="message-content">
                    <div class="text">
                        <el-input
                            type="textarea"
                            :rows="2"
                            placeholder="请输入内容"
                            :autosize="{ minRows: 4, maxRows: 10}"
                            v-model="textarea">
                        </el-input>
                    </div>
                </div>
            </div>
            <div class="car-btn car-btn-gray">確認送出</div>
        </div>
    </div>
</template>

<script>
import HeadIcon from '../../../layout/headIcon.vue'
export default {
    name:'message',
    components: {
        HeadIcon,
    },
    data() {
        return {
            user:'',
            textarea:'',
            messageMenu: false,
        }
    },
    methods:{
        msgMenuClose(){
            this.messageMenu = !this.messageMenu;
        }
    }
}
</script>

<style lang='scss' scoped>
.member-message {
    display: flex;
    width: 90%;
    margin: 0 auto;
    .message-wrap{
        position: relative;
        width: 100%;
        height: 100%;
    }
    .message-list {
        background: #fbfbfb;
        width: 25%;
        min-width: 280px;
        position: relative;
        .user {
            padding: 30px 5%;
            border-bottom: 1px solid #dddddd;
            display: flex;
            align-items: center;
            line-height: 20px;
            cursor: pointer;
            i {
                width: 60px;
                line-height: 60px;
                border: #999999 2px solid;
                border-radius: 40px;
                text-align: center;
                color: #999;
                font-size: 40px;
            }
            .info {
                margin-left: 10px;
                color: #999;
                width: 100%;
            }
            .unread {
                color: #333333;
                .name {
                    font-weight: bold;
                }
            }
        }
        .el-input{
            width: 90%;
            display: block;
            margin: 20px auto;
        }
    }
    .message-item{
        position: absolute;
        width: 100%;
        overflow-y: auto;
        height: calc(100% - 85px);
    }
    .message-info {
        margin-top: 15px;
        padding: 0 15px 50px 15px;
        .car-content {
            display: flex;
            background: #f4f4f4;
            flex-wrap: wrap;
            border: 1px solid #dddddd;
            border-radius: 20px;
            padding: 20px;
            align-items: center;
            img {
                max-width: 120px;
            }
            .txt {
                width: 60%;
                margin: 0 15px;
                color: #555555;
                .name {
                    font-size: 22px;
                    margin-bottom: 10px;
                    color: #333333;
                    text-decoration: underline;
                }
            }
            .card-money {
                background: #ef6d38;
                line-height: 50px;
                color: #fff;
                font-size: 20px;
                display: flex;
                img {
                    background: #ca582a;
                    padding:10px;
                }
                p {
                    padding: 0 10px;
                    white-space: nowrap;
                }
            }
        }
        .message-box {
            padding: 40px;
            position: relative;
            border-bottom:#eeeeee 1px solid;
            &:hover {
                .el-icon-right {
                    background:#ef6d38;
                }
            }
            &:last-child {
                border-bottom:none;
            }
            .message-user {
                display: flex;
                align-items: center;
                .head-icon {
                color: #ef6d38;
                margin: 0;
                margin-right: 20px;
                }
                .name {
                    color: #ef6d38;
                    font-size: 22px;
                    margin-bottom: 5px;
                }
                .date {
                    color: #999999;
                }
            }
            .message-content {
                max-width: 950px;
                margin-left: 107px;
                width: 80%;
                .title {
                    font-size: 22px;
                    text-decoration: underline;
                    margin-bottom: 20px;
                }
                .text {
                    color: #333333;
                }
            }
            .el-icon-right {
                width: 50px;
                height: 50px;
                color: #fff;
                background: #555555;
                font-size: 40px;
                text-align: center;
                box-sizing: border-box;
                padding-top: 5px;
                position: absolute;
                right: 0;
                transition: all .3s;
                top: calc( 50% - 25px );
            }
        }
        .reply {
            padding-left: 150px;
                .message-user {
                    .head-icon {
                        color: #999999;
                    }
                    .name {
                        color: #999999;
                    }
                }
            }
        .input {
            border-radius: 20px;
            border:#eeeeee 1px solid;
            margin-top: 20px;
        }
        @media screen and (max-width: 768px){
            .el-icon-right {
                display: none;
            }
            .message-box .message-user .el-icon-user-solid {
                width: 40px;
                height: 40px;
                font-size: 40px;
            }
            .message-box .message-content {
                margin-left: 0;
                width: 100%;
                margin-top: 15px;
            }
            .reply {
            padding-left: 40px;
            }
            
        }
    }
    .switch-btn {
        position: fixed;
        top: 10%;
        right: -24px;
        color: #f5f5f5;
        background: #ef6d38;
        border-radius: 0px 5px 5px 0px;
        padding: 20px 5px;
        display: none;
        cursor: pointer;
        margin-top: -28px;
        z-index: 1;
        transition: all .4s;
        &.close {
            &::before{
                display: block;
                transform: rotate(180deg);
            }
        }
        z-index: 2;
    }

    @media screen and (max-width: 768px){ 
        width: auto;
        overflow: auto;
        position: relative;
        display: block;
        overflow: visible;
        .message-info {
            min-width: auto;
            margin: 0 auto;
            width: 90%;
            padding: 5%;
            .car-content {
                text-align: center;
                .txt {
                    width: 100%;
                    margin: 15px 0;
                }
                .card-money,img {
                    margin: auto;
                }
            }
        }
        .message-list{
            position: absolute;
            left: 0;
            top: 0;
            transform: translateX(-100%);
            transition: all .3s;
            height: 100%;
            z-index: 1;
            &.open{
                transform: translateX(0);
            }
        }
        .message-info{
            min-width: auto;
            .message-box{
                padding: 40px 0;
            }
        }
        .switch-btn {
            display: block;
        }
        .message-box.input{
            padding: 20px;
        }
    }
}
</style>


